import { useState, useEffect, useCallback } from "react";
import { Link as RouterLink } from "react-router-dom";
import { Helmet } from "react-helmet-async";
import {
  Box,
  Breadcrumbs,
  Button,
  Container,
  Grid,
  Link,
  Typography,
} from "@material-ui/core";
import { invoiceApi } from "../../__fakeApi__/invoiceApi";
import axios from "axios";
import AllassignPagination from "../../mangement/classes/AllassignPagination";
import useMounted from "../../hooks/useMounted";
import useSettings from "../../hooks/useSettings";
import ChevronRightIcon from "../../icons/ChevronRight";
import DownloadIcon from "../../icons/Download";
import PlusIcon from "../../icons/Plus";
import UploadIcon from "../../icons/Upload";
import gtm from "../../lib/gtm";
import userService from "../apis/userService";
import {customAxios} from "../apis/Axios";

const InvoiceList = () => {
  const mounted = useMounted();
  const { settings } = useSettings();
  const [teacher, setTeacher] = useState([]);
  const [refresh,setRefresh]=useState(false)
  const assaignTeacher = async () => {
    const assignTeacherData = await customAxios.getCall(
      "class/api/assignteacher");
      setTeacher(assignTeacherData);
  };
  useEffect(() => {
    gtm.push({ event: "page_view" });
  }, []);


  useEffect(() => {
    assaignTeacher();
  }, [refresh]);
  return (
    <>
      <Helmet>
        <title>Dashboard: Invoice List | Material Kit Pro</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 4,
        }}
      >
        <Container maxWidth={settings.compact ? "xl" : false}>
          <Grid container justifyContent="space-between" spacing={3}>
            <Grid item>
              <Typography color="textPrimary" variant="h5">
                Assign Class to teacher
              </Typography>
              <Breadcrumbs
                aria-label="breadcrumb"
                separator={<ChevronRightIcon fontSize="small" />}
                sx={{ mt: 1 }}
              >
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard"
                  variant="subtitle2"
                >
                  Dashboard
                </Link>

                <Typography color="textSecondary" variant="subtitle2">
                  Assign Class to teacher
                </Typography>
              </Breadcrumbs>
              <Box
                sx={{
                  mb: -1,
                  mx: -1,
                  mt: 1,
                }}
              >
                <Button
                  color="primary"
                  startIcon={<UploadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  variant="text"
                >
                  Import
                </Button>
                <Button
                  color="primary"
                  startIcon={<DownloadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  variant="text"
                >
                  Export
                </Button>
              </Box>
            </Grid>
          </Grid>
          <Box sx={{ mt: 3 }}>
            <AllassignPagination
              refresh={refresh}
              setRefresh={setRefresh}
              invoices={teacher}
            />
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default InvoiceList;
